import React, { useState } from 'react';
import './App.css';
import { Routes, Route } from "react-router-dom";
import NotFind from './pages/NotFind/Notfind';
import Home from './pages/Home/Home';
import Content from './pages/Content/Content';
import Draw from './pages/Content/Components/Draw/Draw';
import Show from './pages/Content/Components/Show/Show';

import 'antd/dist/antd.css';

// deeplearn.js
import { ENV, Array1D, Array3D, Scalar } from 'deeplearn'


function App() {
  const [weights_cache, setWeights_cache] = useState({}) // 
  // get model
  const fetch_weights = (path) => {
    return new Promise(function (resolve, reject) {
        if (path in weights_cache) {
            resolve(weights_cache[path]);
            return;
        }

        var xhr = new XMLHttpRequest();
        xhr.open("GET", path, true);
        xhr.responseType = "arraybuffer";
        // 
        // xhr.onprogress = function (e) {
        //     progress_cb(e.loaded, e.total);
        // };

        xhr.onload = function (e) {
            if (xhr.status != 200) {
                reject("missing model");
                return;
            }
            var buf = xhr.response;
            if (!buf) {
                reject("invalid arraybuffer");
                return;
            }

            var parts = [];
            var offset = 0;
            while (offset < buf.byteLength) {
                var b = new Uint8Array(buf.slice(offset, offset + 4));
                offset += 4;
                var len = (b[0] << 24) + (b[1] << 16) + (b[2] << 8) + b[3];
                parts.push(buf.slice(offset, offset + len));
                offset += len;
            }

            var shapes = JSON.parse(new TextDecoder("utf8").decode(parts[0]));
            var index = new Float32Array(parts[1]);
            var encoded = new Uint8Array(parts[2]);

            // decode using index
            var arr = new Float32Array(encoded.length);
            for (var i = 0; i < arr.length; i++) {
                arr[i] = index[encoded[i]];
            }

            var weights = {};
            var offset = 0;
            for (var i = 0; i < shapes.length; i++) {
                var shape = shapes[i].shape;
                var size = shape.reduce((total, num) => total * num);
                var values = arr.slice(offset, offset + size);
                var dlarr = Array1D.new(values, "float32");
                weights[shapes[i].name] = dlarr.reshape(shape);
                offset += size;
            }
            weights_cache[path] = weights;
            resolve(weights);
            setWeights_cache(weights_cache) // adapt react
        };
        xhr.send(null);
    });
} // fetch_weights

  return (
    <div className="App">
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path='content' element={<Content />}>
          <Route index path='draw' element={<Draw fetch_weights={fetch_weights} />} />
          <Route path='show' element={<Show weights_cache={weights_cache} setWeights_cache={setWeights_cache}/>} />
        </Route>
        <Route path="*" element={<NotFind />} />
      </Routes>
    </div>
  );
}

export default App;
