import React, { useState } from 'react'
import Canvas from '../../../../Components/Canvas/Canvas'
import { IconButton } from '../../../../utils/IconManage/IconManage'
import { Radio, Space, Progress } from 'antd';


import './Draw.less'
import { useNavigate } from 'react-router-dom';



export default function Draw(props) {
    const cRef = React.createRef()
    const [nowTool, setNowTool] = useState(1);
    const navigate = useNavigate()
    const {fetch_weights} = props
    const [precent, setPrecent] = useState(0)
    const [showPrecent, setShowPrecent] = useState('none')

    const undo = () => {
        cRef.current.undo()
    }

    const process = async () => {
        const data = cRef.current.getData()
        setShowPrecent('block')
        await fetch_weights('/modules/qj_model.pict')
        setPrecent(30)
        await fetch_weights('/modules/ql_model.pict')
        setPrecent(60)
        await fetch_weights('/modules/sm_model.pict')
        setPrecent(90)
        navigate('/content/show', { state: data })
    }

    const random = () => {
        
    }

    const selectTool = (e) => {
        let v = e.target.value
        console.log(v)
        setNowTool(v)
        if (v === 1) {
            cRef.current.Pen()
        } else if (v === 2) {
            cRef.current.Eraser()
        }
    }
    return (
        <div className='Draw'>
            <Canvas
                className='canvas'
                width={'256px'}
                height={'256px'}
                onRef={cRef}
            />
                <Progress percent={precent} status="active" className='progress' style={{display:showPrecent}}/>
            
            <div className="buttons">
                <IconButton onClick={undo}>Undo</IconButton>
                <IconButton onClick={process}>Process</IconButton>
                <IconButton onClick={random}>Random</IconButton>
            </div>
            <div className="tool">
                <span>
                    Choose a Tool
                </span>
                <div className='input'>
                    <Radio.Group onChange={selectTool} value={nowTool}>
                        <Space direction="vertical">
                            <Radio value={2}>Eraser</Radio>
                            <Radio value={1}>Line</Radio>
                        </Space>
                    </Radio.Group>
                </div>
            </div>
        </div>
    )
}
