import { Space } from 'antd'
import React, { useEffect, useState } from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import { IconButton } from '../../../../utils/IconManage/IconManage'
import { Skeleton,Spin } from 'antd'; // 
// deeplearn.js
import { ENV, Array1D, Array3D, Scalar } from 'deeplearn'

import './Show.less'

import IconLoading from '../../../../Assets/miniIcon/loading.gif'


export default function Show(props) {
    const navigate = useNavigate()
    const { state } = useLocation() // edges image 
    const [isLoading, setIsLoading] = useState(false)
    // const [weights_cache, setWeights_cache] = useState({})
    const {weights_cache,setWeights_cache} = props
    const [qjUrl, setQjUrl] = useState()
    const [qlUrl, setQlUrl] = useState()
    const [smUrl, setSmUrl] = useState()
    

    useEffect(() => {
        // setTimeout(() => {
        //     setIsLoading(false)
        // }, 3000);
        setTimeout(() => {
            getResult(state,'/modules/qj_model.pict')
        }, 0);
        setTimeout(() => {
            getResult(state,'/modules/ql_model.pict')
        }, 0);
        setTimeout(() => {
            getResult(state,'/modules/sm_model.pict')
        }, 0);
        
        
    }, [])

    const tryAgain = () => {
        navigate('/content/draw')
    }

    const emailButton = () => {
        setIsLoading(!isLoading)
    }

    // get model
    function fetch_weights(path) {
        return new Promise(function (resolve, reject) {
            if (path in weights_cache) {
                resolve(weights_cache[path]);
                return;
            }

            var xhr = new XMLHttpRequest();
            xhr.open("GET", path, true);
            xhr.responseType = "arraybuffer";
            // 
            // xhr.onprogress = function (e) {
            //     progress_cb(e.loaded, e.total);
            // };

            xhr.onload = function (e) {
                if (xhr.status != 200) {
                    reject("missing model");
                    return;
                }
                var buf = xhr.response;
                if (!buf) {
                    reject("invalid arraybuffer");
                    return;
                }

                var parts = [];
                var offset = 0;
                while (offset < buf.byteLength) {
                    var b = new Uint8Array(buf.slice(offset, offset + 4));
                    offset += 4;
                    var len = (b[0] << 24) + (b[1] << 16) + (b[2] << 8) + b[3];
                    parts.push(buf.slice(offset, offset + len));
                    offset += len;
                }

                var shapes = JSON.parse(new TextDecoder("utf8").decode(parts[0]));
                var index = new Float32Array(parts[1]);
                var encoded = new Uint8Array(parts[2]);

                // decode using index
                var arr = new Float32Array(encoded.length);
                for (var i = 0; i < arr.length; i++) {
                    arr[i] = index[encoded[i]];
                }

                var weights = {};
                var offset = 0;
                for (var i = 0; i < shapes.length; i++) {
                    var shape = shapes[i].shape;
                    var size = shape.reduce((total, num) => total * num);
                    var values = arr.slice(offset, offset + size);
                    var dlarr = Array1D.new(values, "float32");
                    weights[shapes[i].name] = dlarr.reshape(shape);
                    offset += size;
                }
                weights_cache[path] = weights;
                resolve(weights);
                setWeights_cache(weights_cache) // adapt react
            };
            xhr.send(null);
        });
    } // fetch_weights

    const getResult = (imageData, modalPath) => {
        function delay(fn) {
            setTimeout(fn, 0);
        }

        function model(input, weights) {
            const math = ENV.math;

            function preprocess(input) {
                return math.subtract(
                    math.multiply(input, Scalar.new(2)),
                    Scalar.new(1)
                );
            }

            function deprocess(input) {
                return math.divide(
                    math.add(input, Scalar.new(1)),
                    Scalar.new(2)
                );
            }

            function batchnorm(input, scale, offset) {
                var moments = math.moments(input, [0, 1]);
                const varianceEpsilon = 1e-5;
                return math.batchNormalization3D(
                    input,
                    moments.mean,
                    moments.variance,
                    varianceEpsilon,
                    scale,
                    offset
                );
            }

            function conv2d(input, filter, bias) {
                return math.conv2d(input, filter, bias, [2, 2], "same");
            }

            function deconv2d(input, filter, bias) {
                var convolved = math.conv2dTranspose(
                    input,
                    filter,
                    [input.shape[0] * 2, input.shape[1] * 2, filter.shape[2]],
                    [2, 2],
                    "same"
                );
                var biased = math.add(convolved, bias);
                return biased;
            }

            var preprocessed_input = preprocess(input);

            var layers = [];

            var filter = weights["generator/encoder_1/conv2d/kernel"];
            var bias = weights["generator/encoder_1/conv2d/bias"];
            var convolved = conv2d(preprocessed_input, filter, bias);
            layers.push(convolved);

            for (var i = 2; i <= 8; i++) {
                var scope = "generator/encoder_" + i.toString();
                var filter = weights[scope + "/conv2d/kernel"];
                var bias = weights[scope + "/conv2d/bias"];
                var layer_input = layers[layers.length - 1];
                var rectified = math.leakyRelu(layer_input, 0.2);
                var convolved = conv2d(rectified, filter, bias);
                var scale = weights[scope + "/batch_normalization/gamma"];
                var offset = weights[scope + "/batch_normalization/beta"];
                var normalized = batchnorm(convolved, scale, offset);
                layers.push(normalized);
            }

            for (var i = 8; i >= 2; i--) {
                if (i == 8) {
                    var layer_input = layers[layers.length - 1];
                } else {
                    var skip_layer = i - 1;
                    var layer_input = math.concat3D(
                        layers[layers.length - 1],
                        layers[skip_layer],
                        2
                    );
                }
                var rectified = math.relu(layer_input);
                var scope = "generator/decoder_" + i.toString();
                var filter = weights[scope + "/conv2d_transpose/kernel"];
                var bias = weights[scope + "/conv2d_transpose/bias"];
                var convolved = deconv2d(rectified, filter, bias);
                var scale = weights[scope + "/batch_normalization/gamma"];
                var offset = weights[scope + "/batch_normalization/beta"];
                var normalized = batchnorm(convolved, scale, offset);
                // missing dropout
                layers.push(normalized);
            }

            var layer_input = math.concat3D(
                layers[layers.length - 1],
                layers[0],
                2
            );
            var rectified = math.relu(layer_input);
            var filter = weights["generator/decoder_1/conv2d_transpose/kernel"];
            var bias = weights["generator/decoder_1/conv2d_transpose/bias"];
            var convolved = deconv2d(rectified, filter, bias);
            var rectified = math.tanh(convolved);
            layers.push(rectified);

            var output = layers[layers.length - 1];
            var deprocessed_output = deprocess(output);

            return deprocessed_output;
        }

        fetch_weights(modalPath).then(
            (weights) => {
                //   this.progress = null;
                //   update();
                // delay a short period of time so that UI updates before the model uses all the CPU
                delay(() => {
                    // var g = new dl.Graph()

                    // var convert = createContext(SIZE, SIZE, 1);
                    // convert.drawImage(
                    //   this.buffer.canvas,
                    //   0,
                    //   0,
                    //   convert.canvas.width,
                    //   convert.canvas.height
                    // );
                    // var input_uint8_data = convert.getImageData(
                    //   0,
                    //   0,
                    //   SIZE,
                    //   SIZE
                    // ).data;
                    const SIZE = 256
                    var input_uint8_data = state
                    var input_float32_data = Float32Array.from(
                        input_uint8_data,
                        (x) => x / 255
                    );

                    // console.time("render");

                    const math = ENV.math;
                    math.startScope();
                    var input_rgba = Array3D.new(
                        [SIZE, SIZE, 4],
                        input_float32_data,
                        "float32"
                    );
                    var input_rgb = math.slice3D(
                        input_rgba,
                        [0, 0, 0],
                        [SIZE, SIZE, 3]
                    );

                    var output_rgb = model(input_rgb, weights);

                    var alpha = Array3D.ones([SIZE, SIZE, 1]);
                    var output_rgba = math.concat3D(output_rgb, alpha, 2);

                    output_rgba
                        .getValuesAsync()
                        .then((output_float32_data) => {
                            var output_uint8_data = Uint8ClampedArray.from(
                                output_float32_data,
                                (x) => x * 255
                            );

                            var imageData = new ImageData(output_uint8_data, SIZE, SIZE)
                            
                            function imagedata_to_image(imagedata) {
                                var canvas = document.createElement('canvas');
                                var ctx = canvas.getContext('2d');
                                canvas.width = imagedata.width;
                                canvas.height = imagedata.height;
                                ctx.putImageData(imagedata, 0, 0);
                                let url = canvas.toDataURL();
                                if (modalPath === '/modules/qj_model.pict') {
                                    setQjUrl(url)
                                } else if (modalPath === '/modules/ql_model.pict') {
                                    setQlUrl(url)
                                } else if (modalPath === '/modules/sm_model.pict') {
                                    setSmUrl(url)
                                }
                            }
                            imagedata_to_image(imageData)
                            
                            // math.endScope();
                            // console.timeEnd("render");
                        });
                });
            },
            (e) => {
                this.last_failure = e;
                this.progress = null;
            }
        ); // then
    }


    return (
        <div className='Show'>
            <Space direction={"vertical"}>
                <Space size={60} className={'box'} style={{ "width": "100%" }}>
                    <div className="item">
                        <div className='image'>
                            <Spin spinning={!qjUrl} style={{width:250,height:250}} indicator={<img style={{width:'100%',height:'100%',position:'relative',top:0,left:0,margin:0}} src={IconLoading}></img>}>
                                <img src={qjUrl} alt="" />
                            </Spin>
                        </div>
                        <span>
                            Water-ink
                        </span>
                    </div>

                    <div className="item">
                        <div className='image'>
                            <div className='image'>
                                <Spin spinning={!qlUrl}  style={{width:250,height:250}} indicator={<img style={{width:'100%',height:'100%',position:'relative',top:0,left:0,margin:0}} src={IconLoading}></img>}>
                                    <img src={qlUrl} alt="" />
                                </Spin>
                            </div>
                        </div>
                        <span>
                            Blue-green
                        </span>
                    </div>
                    <div className="item">
                        <div className='image'>
                            <div className='image'>
                                <Spin spinning={!smUrl} style={{width:250,height:250}} indicator={<img style={{width:'100%',height:'100%',position:'relative',top:0,left:0,margin:0}} src={IconLoading}></img>}>
                                    <img src={smUrl} alt="" />
                                </Spin>
                            </div>
                        </div>
                        <span>
                            light crimson
                        </span>
                    </div>
                </Space>
                <div className='Buttons'>
                    <Space size={60}>
                        <IconButton onClick={tryAgain}>Try again!</IconButton>
                        <IconButton onClick={emailButton}>Type in ur email</IconButton>
                    </Space>
                </div>
            </Space>

        </div>
    )
}
